<template>
  <div class="main-div">
    <div class="logo"></div>
    <div class="description-container">
      <p class="description">你接受了阿勤的任務，請進入台鐵基隆站，搭乘區間車往三坑。</p>
      <br>
      <p class="description">本任務有語音內容，無視覺內容，建議使用耳機聆聽。</p>
    </div>
    <div id="button" @click="nextPage"></div>
  </div>
</template>

<script>
export default {
  name: 'TrainJourney',
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      }
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', false)
  },
  methods: {
    nextPage () {
      this.$router.push({name: 'CommuteTrainAudioGuide'})
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    min-height: 100vh;
    width: 100%;
    background-color: black;
    text-align: center;
    position: relative;
    .logo{
      @extend centerContainImage;
      margin-top: 15%;
      display: inline-block;
      width: 250px;
      height: 250px;
      background-image: url('~@/assets/images/commute/ticket.png');
    }
    .description {
      @extend transferQuestionGreenText;
      margin: 10px 20px;
    }

    #button {
      display: inline-block;
      margin-top: 0px;
      width: 45px;
      height: 45px;
      background-image: url('~@/assets/images/go-right-arrow.png');
      background-size: contain;
    }
  }
</style>



